<template>
  <div>
  <div id="mapContainer"></div>
</div>
</template>

<script>
import mapboxgl from "mapbox-gl";

export default {
  name: "BaseMap",
  data() {
    return {
      accessToken: 'pk.eyJ1Ijoidm9ydGV4eGkiLCJhIjoiY2tpcHN6bThhMDhzczM0cGpuMnI5ZDU2NCJ9.YY6lQf6EtUaXlbjJskmjow'
    };
  },//data
  mounted() {
    mapboxgl.accessToken = this.accessToken;

    var map = new mapboxgl.Map({
      container: "mapContainer",
      style: 'mapbox://styles/mapbox/dark-v10',
center: [4.342, 50.838],
zoom: 16,
pitch: 70,
bearing: -17.6,
antialias: true
      
    });

    // The 'building' layer in the mapbox-streets vector source contains building-height
// data from OpenStreetMap.
map.on('load', function () {
// Insert the layer beneath any symbol layer.
var layers = map.getStyle().layers;
 
var labelLayerId;
for (var i = 0; i < layers.length; i++) {
if (layers[i].type === 'symbol' && layers[i].layout['text-field']) {
labelLayerId = layers[i].id;
break;
}
}
 
map.addLayer(
{
'id': '3d-buildings',
'source': 'composite',
'source-layer': 'building',
'filter': ['==', 'extrude', 'true'],
'type': 'fill-extrusion',
'minzoom': 15,
'paint': {
'fill-extrusion-color': '#aaa',
 
// use an 'interpolate' expression to add a smooth transition effect to the
// buildings as the user zooms in
'fill-extrusion-height': [
'interpolate',
['linear'],
['zoom'],
15,
0,
15.05,
['get', 'height']
],
'fill-extrusion-base': [
'interpolate',
['linear'],
['zoom'],
15,
0,
15.05,
['get', 'min_height']
],
'fill-extrusion-opacity': 0.6
}
},
labelLayerId
);
});
  },//mounted
};
</script>
<style>
  
  #mapContainer { position: fixed; top: 0; bottom: 0; width: 100%; height: 100%; padding: 0; margin: 0; -webkit-filter: blur(0.7px); /* Safari 6.0 - 9.0 */
  filter: blur(0.7px);}
</style>

